import {action, observable} from "mobx";
import axios from "axios";
import {TelegramUser} from "./component/TelegramLogin";
import {UserState} from "./model/UserState";

const state = observable({loginForm: null as unknown as ILoginForm});

const checkUserState = action(async (telegramUserId: string, guid: string) => {
    await axios.get(`/User/LoginByLink?TelegramUserId=${telegramUserId}&Guid=${guid}`).then(response => {
        state.loginForm = response.data;
    })
})

const sendTelegramUserAsync = action(async (user: TelegramUser) => {
    console.log(user)

    await axios.post("/User/Auth", user)
})

export {state, checkUserState, sendTelegramUserAsync}

interface ILoginForm{
    state: UserState
}
